@font-face {
  font-family: Matter-Bold;
  src: url(https://cdn.botpenguin.com/assets/website/fonts/Matter/Matter-Bold.ttf);
  font-display: swap;
}
@font-face {
  font-family: Matter-Light;
  src: url(https://cdn.botpenguin.com/assets/website/fonts/Matter/Matter-Light.ttf);
  font-display: swap;
}
@font-face {
  font-family: Matter-Regular;
  src: url(https://cdn.botpenguin.com/assets/website/fonts/Matter/Matter-Regular.ttf);
  font-display: swap;
}
@font-face {
  font-family: Matter-RegularItalic;
  src: url(https://cdn.botpenguin.com/assets/website/fonts/Matter/Matter-RegularItalic.ttf);
  font-display: swap;
}
@font-face {
  font-family: Matter-SemiBold;
  src: url(https://cdn.botpenguin.com/assets/website/fonts/Matter/Matter-SemiBold.ttf);
  font-display: swap;
}
@font-face {
  font-family: Matter-Medium;
  src: url(https://cdn.botpenguin.com/assets/website/fonts/Matter/Matter-Medium.ttf);
  font-display: swap;
}

body {
  margin: 0;
  font-family: "Matter-Regular", sans-serif;
}


h1,
h2 {
  font-family: Matter-Bold;
  font-size: 39px;
}
.italic-super {
  font-family: Matter-RegularItalic;
}
p {
  font-size: 19px;
  font-family: Matter-Light;
}
@media (max-width: 1200px) {
  h1,
  h2 {
    font-size: 31px;
  }
  p {
    font-size: 15px;
  }
}
@media (max-width: 930px) {
  h1,
  h2 {
    font-size: 31px;
  }
  p {
    font-size: 16px;
  }
}
@media (max-width: 500px) {
  h1,
  h2 {
    font-size: 30px;
  }
  p {
    font-size: 16px;
  }
}
