.main_ui_container{
        height: 100vh;
        display: flex;
        flex-direction: column;
        background: white;
        border-radius: 20px;
        box-shadow: 0 0 3px 1px hsla(0, 0%, 57.3%, 0.3);
        z-index: 100000000000000;
}
@media screen and (min-width: 600px) {
    .main_ui_container {
    max-height:600px;
    max-width: 500px;
    position : fixed;;
    bottom: 20px;
    right: 20px;
    z-index: 100000000000000;
    }
}

.custom_scrollbar::-webkit-scrollbar {
  width: 10px;
}

.custom_scrollbar::-webkit-scrollbar-thumb {
  background: #118cfc99;
  border-radius: 20px;
}

.custom_scrollbar::-webkit-scrollbar-thumb:hover {
  background: #118cfc99;
}

.wrapper_style {
  background: #fff;
  z-index: 2147483647;
  width: 360px;
  height: 500px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 3px 1px hsla(0, 0%, 57.3%, 0.3);
  left: auto;
  display: none;
}

.action_btn p{
    font-size: 1rem;
    font-family: "Matter-Medium";
    font-weight: 400;
    line-height: 1.5;
}

.message_text p{
font-family: 'Matter-Medium';

}
